import {PageHeader} from "../PageHeader";
import * as React from "react";
import css from "./DevicePage.module.css";
import {DeviceCard} from "./DeviceCard";
import {DeviceDetails} from "../../../types/internal-types";
import {DashboardContext} from "../../Providers";
import {SkeletonShimmer} from "../../SkeletonShimmer";

export interface OwnProps {}

const innitialState = {
  deviceList: [] as DeviceDetails[]
};

export class DevicesPage extends React.Component<
  OwnProps,
  typeof innitialState
> {
  readonly state = innitialState;
  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;

  async componentDidMount() {
    const data: DeviceDetails[] = await this.context.dashboardServerApi.getDeviceList();
    this.setState({deviceList: data});
  }

  render() {
    const {deviceList} = this.state;

    return (
      <section className={css.pageWrapper}>
        <PageHeader>MY DEVICES</PageHeader>
        <div className={css.pageContentWrapper} id="device_context">
          {deviceList.map((deviceData, index) => (
            <DeviceCard key={index} cardIndex={index} {...deviceData} />
          ))}
        </div>
      </section>
    );
  }
}
