import * as React from "react";
import ReactDOM from "react-dom";
import {DashboardContext} from "../../../Providers";
import css from "./NodeSettings.module.css";
import {UUID} from "../../../../types/internal-types";

interface OwnProps {
  deviceUuid: UUID;
}

const ownState = {
  html: "",
  script: "",
  isLoadingFiles: true
};

export class NodeSettings extends React.Component<OwnProps, typeof ownState> {
  readonly state = ownState;

  static contextType = DashboardContext;
  context!: React.ContextType<typeof DashboardContext>;

  private iframeRoot = React.createRef<HTMLIFrameElement>();

  async componentDidMount() {
    const html = await this.context.dashboardServerApi.getNodeSettingsHtml(
      this.props.deviceUuid
    );
    const script = await this.context.dashboardServerApi.getNodeSettingsScript(
      this.props.deviceUuid
    );
    this.setState({html, script, isLoadingFiles: false});
  }

  componentDidUpdate() {
    this.createIframeContent();
  }

  decodeDomProperty(hexx: string) {
    const hex = hexx.toString(); //force conversion
    let str = "";
    for (let i = 0; i < hex.length && hex.substr(i, 2) !== "00"; i += 2)
      str += String.fromCharCode(parseInt(hex.substr(i, 2), 16));
    return str;
  }

  createIframeContent() {
    if (this.iframeRoot.current) {
      const htmlString = this.decodeDomProperty(this.state.html);
      const iframeWrapper: any = ReactDOM.findDOMNode(this.iframeRoot.current);
      if (iframeWrapper && iframeWrapper.contentDocument) {
        iframeWrapper.contentDocument.open();
        iframeWrapper.contentDocument.write(htmlString);
        iframeWrapper.contentDocument.close();
      }
    }
  }

  render() {
    return (
      <>
        <div className={css.nodeSettingsHeader}>DEVICE SETTINGS</div>
        {this.state.isLoadingFiles ? (
          <div className={css.loaderWrapper}>
            <div className={css.loading}>Loading</div>
          </div>
        ) : (
          <>
            <div className={css.nodeSettingsContent}>
              <iframe
                style={{display: "block", width: "100%", height: "100%"}}
                ref={this.iframeRoot}
              />
            </div>
          </>
        )}
      </>
    );
  }
}
